*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

body {
  margin: 0 auto;
  overflow-x: hidden;

  font-weight: 400;
  font-size: 18px;
  line-height: 28px;
  font-family: $font;
  color: $main-text;
  background-color: $main-bg;
}
